body {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    margin: 0;
}

#root {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

main {
    flex: 1;
}

.page-content {
    padding: 15px;
}

::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-track {
    background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
    background: #282c34;
}

::-webkit-scrollbar-thumb:hover {
    background: rgb(40, 44, 52, 80%);
}